	module.exports = {"data":{"markdownRemark":{"html":"<p>Audun Iversen studerte ved Musikkhøgskolen i Oslo, Hochschule für Musik und Theater Felix Mendelssohn-Bartholdy i Leipzig og ved Operaakademiet i København. I juli 2007 kom han til finalen i Hans Gabor Belvedere Singing Competition i Wien og samme år gikk han helt til topps i Dronning Sonja Internasjonale Musikkonkurranse.</p>\n<p>Audun ble raskt oppdaget også utenfor landets grenser, og har opptrådt på steder som Royal Opera House Covent Garden, English National Opera, San Francisco Opera, Teatro dell’Opera di Roma, Maggio Musicale Fiorentino i Firenze, Deutsche Oper Berlin, Theater an der Wien, Bolsjoi-teateret, Opéra de Lille, Théâtre de Caen, Glyndebourne Festival, Kongelige teater i København og Den Norske Opera &#x26; Ballett.</p>\n<p>Blant hans operatiske roller kan nevnes Greven i Figaros bryllup, Silvio i I Pagliacci, Marcello i La bohème, Sharpless i Madama Butterfly, Figaro i Barberen i Sevilla, tittelrollen i Eugene Onegin, og Albert i Werther. Audun Iversen er også en aktiv konsertsanger, og opptrer jevnlig i konserthaller både i Europa og USA. I mai 2014 gjorde han Silvio i Pagliacci ved Kongelige Teater, og i august Grev Luna i Il trovatore ved Opera Hedeland.</p>\n<p>Videre har han hatt oppdrag ved Lyric Opera of Chicago og San Francisco Opera.</p>\n<p>Foto: Tonje Eliasson.</p>","timeToRead":1,"excerpt":"Audun Iversen studerte ved Musikkhøgskolen i Oslo, Hochschule für Musik und Theater Felix Mendelssohn-Bartholdy i Leipzig og ved…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/artister/audun-iversen.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":null,"composedin":null,"startDate":null,"startTime":null,"location":null,"artists":null,"medialink":"https://www.youtube.com/embed/m0dO-YV784k","title":"Audun Iversen","dedicatedTo":null,"author":null,"date":"2018-05-07T12:00:00.048Z","category":"artister","tags":["sanger"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":2,"src":"/static/AudunIversen-9b54a49865c30e10a7f1b3f82287fbae-f604b.jpg","srcSet":"/static/AudunIversen-9b54a49865c30e10a7f1b3f82287fbae-fe940.jpg 480w,\n/static/AudunIversen-9b54a49865c30e10a7f1b3f82287fbae-f604b.jpg 600w","sizes":"(max-width: 600px) 100vw, 600px"}}}},"fields":{"slug":"/artister/audun-iversen/","artist":null,"opuses":[{"frontmatter":{"title":"50","category":"opus","workname":"Scener fra Olav Trygvason","thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/opus50-e13c97686df40c018e00c661efb4eceb-466fa.jpg","srcSet":"/static/opus50-e13c97686df40c018e00c661efb4eceb-27b23.jpg 90w,\n/static/opus50-e13c97686df40c018e00c661efb4eceb-0afbd.jpg 180w,\n/static/opus50-e13c97686df40c018e00c661efb4eceb-466fa.jpg 360w,\n/static/opus50-e13c97686df40c018e00c661efb4eceb-43088.jpg 540w,\n/static/opus50-e13c97686df40c018e00c661efb4eceb-545bd.jpg 720w,\n/static/opus50-e13c97686df40c018e00c661efb4eceb-32fd8.jpg 800w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/opus50/","artist":[{"frontmatter":{"title":"Edward Gardner","tags":["dirigent"]}},{"frontmatter":{"title":"Nina Gravrok","tags":["sanger"]}},{"frontmatter":{"title":"Tone Kummervold","tags":["sanger"]}},{"frontmatter":{"title":"Audun Iversen","tags":["sanger"]}},{"frontmatter":{"title":"Edvard Grieg Kor","tags":["kor"]}},{"frontmatter":{"title":"Edvard Grieg Ungdomskor","tags":["kor"]}},{"frontmatter":{"title":"Bergen Filharmoniske Kor","tags":["kor"]}},{"frontmatter":{"title":"Collegiûm Mûsicûm","tags":["kor"]}},{"frontmatter":{"title":"Bergen Filharmoniske Orkester","tags":["orkester"]}}]}}]}}},"pathContext":{"slug":"/artister/audun-iversen/"}}

